.wa-section-container {
    display: flex;
    flex-direction: column;
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
    max-width: 28rem;
    margin-left: auto;
    margin-right: auto;
    background-color: #1f2937;
    border-radius: 0.5rem;
    box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -1px rgb(0 0 0 / 0.06);
}

.wa-header-container {
    position: relative;
    z-index: 0;
    margin-bottom: 1.25rem;
    max-width: 28rem;
    margin-left: auto;
    margin-right: auto;
    padding-left: 2.75rem;
    padding-right: 2.75rem;
}

.wa-header-title {
    font-size: 1.875rem;
    font-weight: 700;
    color: #fff;
    margin-bottom: 1.25rem;
    margin-top: 0;
}

.wa-header-description {
    font-size: 1rem;
    color: #ffffff;
}

.wa-label {
    position: absolute;
    font-size: 0.875rem;
    color: #4b5563;
    transition: all 0.3s ease-in-out;
    transform: translateY(-1.25rem) scale(0.75);
    top: 0.375rem;
    z-index: -10;
    transform-origin: 0% 0%;
}

.wa-input:focus+.wa-label,
.wa-input:not(:placeholder-shown)+.wa-label {
    font-weight: 500;
    transform: translateY(-1.5rem) scale(0.75);
    color: #3b82f6;
}

.wa-input:placeholder-shown+.wa-label {
    transform: translateY(-0.75rem) scale(1);
}

.wa-label.wa-label-selected {
    font-weight: 500;
    transform: translateY(-1.5rem) scale(0.75);
    color: #3b82f6;
}

.wa-select:focus+.wa-label {
    color: #3b82f6;
    transform: translateY(-1.9rem) scale(1);
}

.wa-input {
    display: block;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 0px;
    padding-right: 0px;
    width: 100%;
    font-size: 0.875rem;
    color: #ffffff;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid #4b5563;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.wa-input:focus {
    outline: none;
    border-bottom-color: #3b82f6;
    box-shadow: none;
}

textarea.wa-input {
    height: 4rem;
}

.wa-select {
    background-color: transparent;
    border: none;
    border-bottom: 2px solid #4b5563;
    color: #fff;
    font-size: 0.875rem;
    border-radius: 0;
    padding: 0.625rem;
    padding-left: 0;
    padding-right: 2rem;
    width: 100%;
    display: block;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: transparent url("data:image/svg+xml;utf8,<svg viewBox='0 0 140 140' width='14' height='14' xmlns='http://www.w3.org/2000/svg'><g><path d='m121.3,34.6c-1.6-1.6-4.2-1.6-5.8,0l-51,51.1-51.1-51.1c-1.6-1.6-4.2-1.6-5.8,0-1.6,1.6-1.6,4.2 0,5.8l53.9,53.9c0.8,0.8 1.8,1.2 2.9,1.2 1,0 2.1-0.4 2.9-1.2l53.9-53.9c1.7-1.6 1.7-4.2 0.1-5.8z' fill='white'/></g></svg>") no-repeat;
    background-position: right 5px top 50%;
    cursor: pointer;
}

.wp-select-no-option {
    color: #4b5563;
}

.wa-select:focus {
    color: #fff;
}

.wa-select-margin {
    margin-top: 0.4rem;
    margin-bottom: 0.8rem;
}

.wa-select:focus {
    outline: none;
    border-radius: 0.5rem;
    border-color: #2563eb;
    box-shadow: 0 0 0 0.25rem #60a5fa;
    background-color: #374151;
    padding-left: 0.625rem;
}

.dark .wa-select {
    border-color: #4b5563;
    color: #e5e7eb;
}

.dark .wa-select::placeholder {
    color: #6b7280;
}

.dark .wa-select:focus {
    border-color: #3b82f6;
    box-shadow: 0 0 0 0.25rem #3b82f6;
}

.wa-select option {
    background-color: #ffffff;
    color: #111827;
}

.dark .wa-select option {
    background-color: #374151;
    color: #e5e7eb;
}

.wa-form-container {
    max-width: 28rem;
    margin-left: 2.75rem;
    margin-right: 2.75rem;
}

.wa-grid-cols-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}

@media (min-width: 768px) {
    .wa-grid-cols-2 {
        gap: 1.5rem;
    }
}

.wa-default-container {
    position: relative;
    z-index: 0;
    width: 100%;
    margin-bottom: 1.50rem;
}

.wa-button {
    color: #edeff3;
    background-color: #1d4ed8;
    font-weight: bold;
    border-radius: 0.5rem;
    font-size: 0.875rem;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    padding-top: 0.625rem;
    padding-bottom: 0.625rem;
    text-align: center;
    transition: background-color 0.15s ease-in-out;
    border: solid 1px #2563eb;
    cursor: pointer
}

.wa-button:hover {
    background-color: #0c215a;
    border-color: #98aadb;
    outline: none;
    box-shadow: 0 0 0 0.15rem #98aadb;
}

.wa-button:focus {
    outline: none;
    box-shadow: 0 0 0 0.25rem #3b82f6;
}

@media (min-width: 640px) {
    .wa-button {
        width: auto;
    }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px #1f2937 inset !important;
}

input:-webkit-autofill {
    -webkit-text-fill-color: #fff !important;
}

input[type="date"]::-webkit-calendar-picker-indicator {
    filter: invert(1);
}